<template>
<div id="soubox">
  <div class="souboxs">
<input type="text" placeholder="搜索歌曲/歌手/专辑" v-model="val"><a href="javascript:;" @click="soubtn">搜</a>
  </div>
  <ul class="uls">
    <li v-for="item in sous" :key="item.title">{{item.title}}</li>
  </ul>
  <div class="songsbox">
    <div class="songs">
 <ul class="ulss">
    <li v-for="item in sousongs" :key="item.id" @click="click3(item.id,item.ar[0].name,item.name)" :class="{aactive:activesss==item.id}">
      <span><img :src="item.al.picUrl">{{item.name}}</span>
      <span>{{item.ar[0].name}}</span>
      <span class="span">{{(Math.floor(item.dt/1000/60))+':'+((Math.floor(item.dt/1000%60))>=10?(Math.floor(item.dt/1000%60)):'0'+(Math.floor(item.dt/1000%60)))}}</span>
      <span>《{{item.al.name}}》</span>
      </li>
  </ul>
    </div>
  </div>
</div>
  <audio :src="play" controls loop="loop" preload="auto" autoplay="autoplay" id="mu"></audio>
  <div class="gcbox">
    <p id="ppp">{{namessss}} - {{namesss}}</p>
        <p class="back"></p>
        <ul class="gcul">
          <li v-for="i in lrrc" :key="i.t" class="gcli"><span class="gcsp">{{Number(i.t).toFixed(2)}}</span>{{i.c}}</li>             
        </ul>
    </div>
</template>


<script>
let gctime=null
import request from '../api/axios.js'
export default {
  name: 'Wyy',
  components: {
  },

  data(){
    return {
        sous:[{title:"单曲"},
        {title:"歌手"},
        {title:"时长"},
        {title:"专辑"}],
          actives:'',
          activess:'',
          activesss:'',
          names:'',
          songs:'',
          play:'',
          show:false,
          val:'',
          sousongs:'',
          lrrc:'',
          namesss:'',
          namessss:''

          }
  },
  methods: {
    soubtn(){
      console.log(this.val)
       let date=new Date()
       request({
       method:'POST',
       url:'http://localhost:3000/cloudsearch?'+date.getTime(),
       data:{
            keywords:this.val
       }
     })
     .then((res)=>{
      console.log(res.data.result.songs)
      this.sousongs=res.data.result.songs
     })
    },
     click3(id,name,names){
     this.activesss=id
      let date=new Date()
       request({
       method:'POST',
       url:'http://localhost:3000/song/url?'+date.getTime(),
       data:{
            id:id,
            limit:30
       }
     })
     .then((res)=>{
       console.log(res)
      this.play=res.data.data[0].url
      this.namesss=name
      this.namessss=names
     })
      request({
          method:'post',
          url:'http://localhost:3000/lyric?'+date.getTime(),
          data:{
              id :id,
          }
        })
        .then((res)=>{
          this.lyrc=res.data.lrc.lyric
          let lrc=this.lyrc
          let oLRC = {
              ti: "", //歌曲名
              ar: "", //演唱者
              al: "", //专辑名
              by: "", //歌词制作人
              offset: 0, //时间补偿值，单位毫秒，用于调整歌词整体位置
              ms: [] //歌词数组{t:时间,c:歌词}
          };
          function createLrcObj(lrc) {
              if(lrc.length==0) return;
              let lrcs = lrc.split('\n');//用回车拆分成数组
              for(let i in lrcs) {//遍历歌词数组
                  lrcs[i] = lrcs[i].replace(/(^\s*)|(\s*$)/g, ""); //去除前后空格
                  let t = lrcs[i].substring(lrcs[i].indexOf("[") + 1, lrcs[i].indexOf("]"));//取[]间的内容
                  let s = t.split(":");//分离:前后文字
                  if(isNaN(parseInt(s[0]))) { //不是数值
                      for (let i in oLRC) {
                          if (i != "ms" && i == s[0].toLowerCase()) {
                              oLRC[i] = s[1];
                          }
                      }
                  }else { //是数值
                      let arr = lrcs[i].match(/\[(\d+:.+?)\]/g);//提取时间字段，可能有多个
                      let start = 0;
                      for(let k in arr){
                          start += arr[k].length; //计算歌词位置
                      }
                      let content = lrcs[i].substring(start);//获取歌词内容
                      for (let k in arr){
                          let t = arr[k].substring(1, arr[k].length-1);//取[]间的内容
                          let s = t.split(":");//分离:前后文字
                          oLRC.ms.push({//对象{t:时间,c:歌词}加入ms数组
                              t: (parseFloat(s[0])*60+parseFloat(s[1])).toFixed(3),
                              c: content
                          });
                      }
                  }
              }
              oLRC.ms.sort(function (a, b) {//按时间顺序排序
                  return a.t-b.t;
              });
          }
          createLrcObj(lrc);
          this.lrrc=oLRC.ms
          gc()
        })
     }
  },
}
function gc(){
  clearInterval(gctime)
  gctime=setInterval(()=>{
      let gcsp=document.querySelectorAll('.gcsp')
      let gcul=document.querySelectorAll('.gcul')
      let gcli=document.querySelectorAll('.gcli')
      let mu=document.getElementById('mu')
      let ti=mu.currentTime.toFixed(2)
      for(let i=0;i<gcsp.length;i++){
          if(ti==Number(gcsp[i].innerHTML)){
              gcul[0].style.top=`${200-i*50}px`
          }
          gcli[i].onclick=function(){
            mu.currentTime=Number(gcsp[i].innerHTML)
          }
      }
  },9)   
}
</script>

